@import url(../../global.css);

.button {
    font-size: 2.4rem;
    padding: 15px 30px;
    font-weight: 600;
}

.button:first-child {
    background-color: var(--white-color);
    color: var(--black-color);
}

.button:first-child:hover {
    background-color: rgb(187, 184, 184);
}
